  color: #43B02A;
}

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkbox input:checked ~ .checkbox__checkmark {
  background-color: #ffffff;
}

.checkbox input:checked ~ .checkbox__checkmark:after {
  opacity: 1;
  content: url(../../img/svg/tick.svg);
}

.checkbox .checkbox__checkmark {
  position: absolute;
  top: 1px;
  left: 2px;
  background-color: #ffffff;
  width: 21px;
  height: 21px;
  border-radius: 2px;
  border: 2px solid #686868;
