  .checkbox .checkbox__checkmark {
    top: 4px;
  }
}

/* tick */
.checkbox .checkbox__checkmark:after {
  content: "";
  position: absolute;
  left: 5px;
  top: -3px;
  width: 15px;
  height: 13px;
  background-color: #ffffff;
  opacity: 0;
}

