.account__arhive-button {
  border: none;
  position: absolute;
  top: 10px;
  right: 23px;
  width: 19px;
  height: 19px;
  cursor: pointer;
  transition: opacity ease 0.5s;
  background-color: inherit;
}
.account__arhive-button:hover {
  opacity: 1;
}
@media (min-width: 768px) {
  .account__arhive-button {
    top: 15px;
  }
}

.account__arhive-button::before,
.account__arhive-button::after {
  content: "";
  position: absolute;
  top: 10px;
  display: block;
  width: 24px;
  height: 3px;
  background: #292929;
}

.account__arhive-button::before {
  transform: rotate(45deg);
}

.account__arhive-button::after {
  transform: rotate(-45deg);
}

